import http from '../axios';
import qs from 'query-string';
import { RUBIX_APP_TYPES } from '../constants/appConstant';
import * as systemSettingService from './systemSettingService';
import { exportENV, exportCSV, exportJSON } from '../utils/utils';

const endpoint = '/rubix-app';

export const fetchApps = () => {
  const queryParams = qs.stringify({
    browser_download_url: true,
    latest_version: true,
  });
  return http.get(`${endpoint}?${queryParams}`);
};

export const fetchAllApps = () => {
  return systemSettingService.fetchSystemServices();
};

export const fetchAllInstalledApps = () => {
  return http.get(`${endpoint}`).then(r => r.filter(appDetail => appDetail && appDetail.is_installed));
};

export const fetchSystemDefaultApps = () => {
  return systemSettingService
    .fetchSystemServices()
    .then(r => r.filter(appDetail => appDetail && appDetail.is_installed));
};

export const fetchAppReleases = appName => {
  return http.get(`${endpoint}/releases/${appName}`);
};

export const fetchAppStatus = (appName, options = {}) => {
  return http.get(`${endpoint}/stats/${appName}`, options);
};

export const uninstallApp = appName => {
  return http.post(`${endpoint}/uninstall`, { service: appName });
};

export const installApp = body => {
  return http.post(`${endpoint}/install`, body);
};

export const downloadApp = body => {
  return http.post(`${endpoint}/download`, body);
};

export const changeServiceStatus = (action, serviceName) => {
  return http.post(`${endpoint}/control`, { action, service: serviceName });
};

export const uploadApp = (file, serviceName, version) => {
  let formData = new FormData();
  formData.append('file', file);
  formData.append('service', serviceName);
  formData.append('version', version);
  return http.post(`${endpoint}/app-upload`, formData, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  });
};

export const deleteAppData = serviceName => {
  return http.post(`${endpoint}/delete-app-data`, { service: serviceName });
};

export const deleteAppDataService = serviceName => {
  return http.post(`${endpoint}/delete-app-data/${serviceName}`, { service: serviceName });
};

export const downloadAppData = (serviceName, appSetting) => {
  const url = `http://${appSetting.globalIp}:${appSetting.rubixServicePort}/api/app/download_data/${serviceName}`;
  return http.get(url, {
    headers: {
      'Content-Type': 'application/zip',
    },
    responseType: 'arraybuffer',
  });
};

export const fetchJSONConfig = (serviceName, config) => {
  return http.get(`${endpoint}/json-config/${serviceName}`, config);
};

export const fetchEnvConfig = (serviceName, config) => {
  return http.get(`${endpoint}/env/${serviceName}`, config);
};

export const fetchAppConfig = (serviceName, appType, config = {}) => {
  if (appType === RUBIX_APP_TYPES.PYTHON_APP) {
    return fetchJSONConfig(serviceName, config);
  } else if (appType === RUBIX_APP_TYPES.FRONTEND_APP) {
    return fetchEnvConfig(serviceName, config);
  }
  throw new Error('App type not supported for editing config');
};

export const saveJSONConfig = (serviceName, config) => {
  return http.put(`${endpoint}/json-config/${serviceName}`, { data: config });
};

export const saveENVConfig = (serviceName, config) => {
  return http.put(`${endpoint}/env/${serviceName}`, { data: config });
};

export const saveAppConfig = (serviceName, appType, config) => {
  if (appType === RUBIX_APP_TYPES.PYTHON_APP) {
    return saveJSONConfig(serviceName, config || {});
  } else if (appType === RUBIX_APP_TYPES.FRONTEND_APP) {
    return saveENVConfig(serviceName, config || '');
  }
  throw new Error('App type not supported for editing config');
};

export const deleteJSONConfig = serviceName => {
  return http.delete(`${endpoint}/json-config/${serviceName}`);
};

export const deleteENVConfig = serviceName => {
  return http.delete(`${endpoint}/env/${serviceName}`);
};

export const deleteAppConfig = (serviceName, appType) => {
  if (appType === RUBIX_APP_TYPES.PYTHON_APP) {
    return deleteJSONConfig(serviceName);
  } else if (appType === RUBIX_APP_TYPES.FRONTEND_APP) {
    return deleteENVConfig(serviceName);
  }
  throw new Error('App type not supported for deletion');
};

export const downloadAppConfig = (serviceName, appType, config = {}) => {
  if (appType === RUBIX_APP_TYPES.PYTHON_APP) {
    return fetchJSONConfig(serviceName, config).then(data => {
      exportJSON(`${serviceName}-config`, JSON.stringify(data));
    });
  } else if (appType === RUBIX_APP_TYPES.FRONTEND_APP) {
    return fetchEnvConfig(serviceName, config).then(data => {
      exportENV(`${serviceName}`, data);
    });
  }
  throw new Error('App type not supported for download config');
};
