<template>
  <v-card :loading="isFetchingAppStatus">
    <v-card-title>
      <v-btn @click="back" icon medium class="white--text mr-1" v-if="multiStepViewConfig[currentView].hasBack">
        <v-icon medium dark> mdi-arrow-left </v-icon>
      </v-btn>
      <span class="sub-headline">{{ multiStepViewConfig[currentView].title(appDetails.display_name) }}</span>
    </v-card-title>
    <v-card-text>
      <v-stepper v-model="currentView">
        <v-stepper-header>
          <v-stepper-step :complete="currentView > 1" step="1"> 1 </v-stepper-step>

          <v-divider></v-divider>
          <v-stepper-step :complete="currentView > 2" step="2"> 2 </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step :complete="currentView > 3" step="3"> 3 </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step step="4"> 4 </v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content step="1">
            <v-row>
              <v-col cols="4" class="text-left">
                <text-field-display label="Name" :value="appDetails.display_name"></text-field-display>
              </v-col>
              <v-col cols="2" class="text-left">
                <text-field-display
                  label="Active"
                  :value="(!isFetchingAppStatus && serviceStatus.time_since ? serviceStatus.time_since : '-') || 'N/A'"
                ></text-field-display>
              </v-col>
              <v-col cols="3" class="text-left">
                <app-status-chip v-if="!isFetchingAppStatus" :state="serviceStatus.state"></app-status-chip>
              </v-col>
              <v-col cols="1" class="text-right">
                <v-btn @click="fetchAppStatus" icon medium color="success" class="white--text">
                  <v-icon medium dark> mdi-refresh </v-icon>
                </v-btn>
              </v-col>
            </v-row>
          </v-stepper-content>
          <v-stepper-content step="2">
            <v-form v-model="valid" ref="uploadForm" lazy-validation>
              <v-row>
                <v-col cols="4" class="text-left">
                  <text-field-display label="Name" :value="appDetails.display_name"></text-field-display>
                </v-col>
                <v-col cols="2" class="text-left">
                  <text-field-display
                    label="Active"
                    :value="
                      (!isFetchingAppStatus && serviceStatus.time_since ? serviceStatus.time_since : '-') || 'N/A'
                    "
                  ></text-field-display>
                </v-col>
                <v-col cols="3" class="text-left">
                  <app-status-chip v-if="!isFetchingAppStatus" :state="serviceStatus.state"></app-status-chip>
                </v-col>
                <v-col cols="1" class="text-right">
                  <v-btn @click="fetchAppStatus" icon medium color="success" class="white--text">
                    <v-icon medium dark> mdi-refresh </v-icon>
                  </v-btn>
                </v-col>
              </v-row>
              <v-row cols="8" class="pa-3">
                <v-file-input
                  v-model="file"
                  :rules="[rules.required]"
                  counter
                  show-size
                  @change="onFileSelect"
                  truncate-length="15"
                ></v-file-input>
              </v-row>
              <v-row>
                <v-col cols="12" class="text-left ml-8">
                  <v-text-field
                    label="Version"
                    type="text"
                    single-line
                    v-model="version"
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-alert v-if="errorMessage" class="text-left" dense outlined type="error">
                {{ errorMessage }}
              </v-alert>
            </v-form>
          </v-stepper-content>

          <v-stepper-content step="3">
            <v-row>
              <v-col cols="6" class="text-left">
                <div class="grey--text mb-2 caption">Name</div>
                <div class="font-weight-medium">
                  {{ appDetails.display_name + ' - ' + appDetails.service }}
                </div>
              </v-col>
              <v-col cols="4" class="text-left">
                <div class="grey--text mb-2 caption">Version</div>
                <div class="font-weight-medium">
                  {{ version }}
                </div>
              </v-col>
              <v-col cols="12" class="text-left" v-if="serviceStatus.app_type === rubixAppTypes.PYTHON_APP">
                <rbx-button
                  v-if="!isDeleteAppData"
                  color="pink"
                  :click="toggleDeleteAppData"
                  title="Delete App Data"
                ></rbx-button>
                <v-card v-if="isDeleteAppData">
                  <v-card-title class="headline"> Confirm? </v-card-title>
                  <v-card-text>Do you want to delete app data?</v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="isDeleteAppData = false"> Cancel </v-btn>
                    <v-btn color="red darken-1" :loading="isDeletingAppData" text @click="deleteAppData"> Yes </v-btn>
                  </v-card-actions>
                </v-card>
              </v-col>
            </v-row>
          </v-stepper-content>

          <v-stepper-content step="4">
            <v-row>
              <v-col cols="12" class="text-left">
                <div class="font-weight-large">Successfully installed {{ appDetails.display_name }} app.</div>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4" class="text-left">
                <div class="grey--text caption mb-2">Name</div>
                <div class="font-weight-medium">
                  {{ appDetails.display_name }}
                </div>
              </v-col>
              <v-col cols="4" class="text-left">
                <div class="grey--text caption mb-2">
                  <span>Version</span>
                </div>
                <div class="font-weight-medium">
                  {{ serviceStatus.version }}
                </div>
              </v-col>
              <v-col cols="4">
                <rbx-button color="success" :click="startApp" :loading="isStartingApp" title="Start App"></rbx-button>
              </v-col>
            </v-row>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-card-text>
    <v-card-actions>
      <v-btn
        color="primary"
        @click="next"
        :loading="multiStepViewConfig[currentView].loader"
        v-if="multiStepViewConfig[currentView].hasForward"
      >
        {{
          typeof multiStepViewConfig[currentView].buttonLabel === 'function'
            ? multiStepViewConfig[currentView].buttonLabel(serviceStatus)
            : multiStepViewConfig[currentView].buttonLabel
        }}
      </v-btn>
      <v-btn @click="cancel" text> Cancel </v-btn>
    </v-card-actions>
  </v-card>

  <!-- <v-progress-circular indeterminate size="64"></v-progress-circular> -->
</template>
<script>
import AppStatusChip from '../Services/AppStatusChip.vue';
import TextFieldDisplay from '../Common/TextFieldDisplay.vue';
import * as appsService from '../../services/appsService';
import RbxButton from '../Common/RbxButton';
import {
  RUBIX_APP_STATUS_CONTROL_ACTIONS,
  ALWAYS_RUNNING_RUBIX_APP,
  RUBIX_APP_TYPES,
} from '../../constants/appConstant';

function shouldStopRubixApp(serviceInfo) {
  return serviceInfo.status && ALWAYS_RUNNING_RUBIX_APP.RUBIX_PLAT !== serviceInfo.service;
}

export default {
  name: 'apps-upload-multi-step',
  props: ['app-details', 'close-dialog'],
  components: { AppStatusChip, TextFieldDisplay, RbxButton },
  data() {
    return {
      isDeleteAppData: false,
      isDeletingAppData: false,
      valid: false,
      currentView: 1,
      version: null,
      serviceStatus: {},
      isFetchingAppStatus: false,
      isStartingApp: false,
      errorMessage: '',
      file: null,
      rubixAppTypes: RUBIX_APP_TYPES,
      multiStepViewConfig: {
        1: {
          back: 1,
          forward: 2,
          title: serviceName => `${serviceName} | App Status`,
          hasBack: false,
          hasForward: true,
          buttonLabel: serviceInfo => {
            if (shouldStopRubixApp(serviceInfo)) {
              return 'Stop';
            }
            return 'Continue';
          },
          loader: false,
        },
        2: {
          back: 1,
          forward: 3,
          title: serviceName => `${serviceName} | Upload App`,
          hasBack: true,
          hasForward: true,
          buttonLabel: 'Upload',
          loader: false,
        },
        3: {
          back: 2,
          forward: 4,
          title: serviceName => `${serviceName} | Install App`,
          hasBack: true,
          hasForward: true,
          buttonLabel: 'Install',
          loader: false,
        },
        4: {
          back: 3,
          forward: 4,
          title: serviceName => `${serviceName} | Installation Complete`,
          hasBack: true,
          hasForward: true,
          buttonLabel: 'Ok',
          loader: false,
        },
      },
      loading: true,
      rules: {
        required: value => !!value || 'Required.',
      },
      versionRules: [value => !!value || 'Version No. is required.'],
    };
  },
  async mounted() {
    await this.fetchAppStatus();
  },
  methods: {
    back() {
      this.currentView = this.currentView - 1;
    },
    cancel() {
      this.closeDialog(true);
    },
    async next(step) {
      const currentView = this.currentView;
      switch (currentView) {
        case 1:
          this.stopAndContinue();
          break;
        case 2:
          this.upload();
          break;
        case 3:
          this.install();
          break;
        case 4:
          this.continue();
          break;
        default:
          this.currentView++;
          break;
      }
    },
    stopAndContinue() {
      const serviceStatus = this.serviceStatus.status;
      const serviceName = this.serviceStatus.service;
      if (shouldStopRubixApp(this.serviceStatus)) {
        this.spinLoader(true);
        appsService
          .changeServiceStatus(RUBIX_APP_STATUS_CONTROL_ACTIONS.STOP, serviceName)
          .then(data => {
            this.spinLoader(false);
            this.currentView++;
            return this.fetchAppStatus();
          })
          .catch(err => {
            this.spinLoader(false);
          });
      } else {
        this.currentView++;
      }
    },
    startApp() {
      const serviceName = this.serviceStatus.service;
      this.isStartingApp = true;
      appsService
        .changeServiceStatus(RUBIX_APP_STATUS_CONTROL_ACTIONS.START, serviceName)
        .then(data => {
          this.$toasted.show(`Successfully started ${serviceName}`);
          this.continue();
          return this.fetchAppStatus();
        })
        .catch(err => {
          this.$toasted.show(`Could not start ${serviceName}`, { type: 'error' });
        })
        .finally(() => {
          this.isStartingApp = false;
        });
    },
    continue() {
      this.closeDialog(true);
    },
    spinLoader(value) {
      this.multiStepViewConfig[this.currentView].loader = value;
    },
    upload() {
      const valid = this.$refs.uploadForm.validate();
      if (!valid) {
        return;
      }
      this.spinLoader(true);
      appsService
        .uploadApp(this.file, this.appDetails.service, this.version)
        .then(res => {
          this.currentView++;
        })
        .catch(err => {
          if (err && err.message) {
            this.errorMessage = err.message;
          }
        })
        .finally(() => {
          this.spinLoader(false);
        });
    },
    install() {
      this.spinLoader(true);
      const data = {
        service: this.appDetails.service,
        version: this.version,
      };

      appsService
        .installApp(data)
        .then(res => {
          this.spinLoader(false);
          this.fetchAppStatus();
          this.currentView++;
          this.errorMessage = '';
        })
        .catch(err => {
          this.spinLoader(false);
          if (shouldStopRubixApp(this.serviceStatus)) {
            this.currentView--;
          } else {
            this.currentView++;
          }
          if (err && err.message && shouldStopRubixApp(this.serviceStatus)) {
            this.errorMessage = err.message;
          }
        });
    },
    fetchAppStatus() {
      this.isFetchingAppStatus = true;
      return appsService
        .fetchAppStatus(this.appDetails.service, { restrictToast: true })
        .then(data => {
          this.serviceStatus = data;
        })
        .catch(err => {
          this.serviceStatus = {};
        })
        .finally(() => {
          this.isFetchingAppStatus = false;
        });
    },
    onFileSelect() {
      if (this.file && this.file.name) {
        const fileName = this.file.name;
        const versionRegex = new RegExp('[0-9].[0-9].[0-9]');
        let versionNumber = fileName.match(versionRegex, '');
        if (versionNumber && Array.isArray(versionNumber)) {
          versionNumber = versionNumber[0];
          this.version = 'v' + versionNumber;
        }
      }
    },
    deleteAppData() {
      this.isDeletingAppData = true;
      appsService
        .deleteAppData(this.serviceStatus.service)
        .then(() => {
          this.$toasted.show('Successfully deleted app data');
          this.isDeleteAppData = false;
        })
        .catch(() => {
          this.$toasted.show('Cannot deleted app data', { type: 'error' });
        })
        .finally(() => {
          this.isDeletingAppData = false;
        });
    },
    toggleDeleteAppData() {
      this.isDeleteAppData = !this.isDeleteAppData;
    },
  },
};
</script>
