<template>
  <v-card :loading="isFetchingAppStatus">
    <v-card-title>
      <v-btn @click="back" icon medium class="white--text mr-1" v-if="multiStepViewConfig[currentView].hasBack">
        <v-icon medium dark> mdi-arrow-left </v-icon>
      </v-btn>
      <span class="headline">{{ multiStepViewConfig[currentView].title }}</span>
    </v-card-title>
    <v-card-text>
      <v-stepper v-model="currentView">
        <v-stepper-header>
          <v-stepper-step :complete="currentView > 1" step="1"> 1 </v-stepper-step>

          <v-divider></v-divider>
          <v-stepper-step :complete="currentView > 2" step="2"> 2 </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step :complete="currentView > 3" step="3"> 3 </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step step="4"> 4 </v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content step="1">
            <v-row>
              <v-col cols="4" class="text-left">
                <text-field-display label="Name" :value="appDetails.display_name"></text-field-display>
              </v-col>
              <v-col cols="3" class="text-left">
                <text-field-display
                  label="Active"
                  :value="(!isFetchingAppStatus && serviceStatus.time_since ? serviceStatus.time_since : '-') || 'N/A'"
                ></text-field-display>
              </v-col>
              <v-col cols="3" class="text-left">
                <app-status-chip :state="serviceStatus.state"></app-status-chip>
              </v-col>
              <v-col cols="1" class="text-right">
                <v-btn @click="fetchAppStatus" icon medium color="success" class="white--text">
                  <v-icon medium dark> mdi-refresh </v-icon>
                </v-btn>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4" class="text-left">
                <text-field-display
                  label="Version"
                  :value="!isFetchingAppStatus && serviceStatus.version ? serviceStatus.version : '-'"
                ></text-field-display>
              </v-col>
            </v-row>
          </v-stepper-content>
          <v-stepper-content step="2">
            <v-form v-model="valid" ref="downloadForm" lazy-validation>
              <v-row>
                <v-col cols="4" class="text-left">
                  <text-field-display label="Name" :value="appDetails.display_name"></text-field-display>
                </v-col>
                <v-col cols="3" class="text-left">
                  <text-field-display
                    label="Active"
                    :value="
                      (!isFetchingAppStatus && serviceStatus.time_since ? serviceStatus.time_since : '-') || 'N/A'
                    "
                  ></text-field-display>
                </v-col>
                <v-col cols="3" class="text-left">
                  <app-status-chip :state="serviceStatus.state"></app-status-chip>
                </v-col>
                <v-col cols="1" class="text-right">
                  <v-btn @click="fetchAppStatus" icon medium color="success" class="white--text">
                    <v-icon medium dark> mdi-refresh </v-icon>
                  </v-btn>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="4" class="text-left">
                  <text-field-display
                    label="Version"
                    :value="!isFetchingAppStatus && serviceStatus.version ? serviceStatus.version : '-'"
                  ></text-field-display>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12">
                  <v-combobox
                    :loading="loading"
                    :items="appReleases"
                    label="Select Release Version"
                    hideSelected
                    clearable
                    v-model="selectedVersion"
                    item-text="name"
                    item-value="tag_name"
                    :rules="versionRules"
                  >
                  </v-combobox>
                </v-col>
              </v-row>
            </v-form>
          </v-stepper-content>

          <v-stepper-content step="3">
            <v-row>
              <v-col cols="6" class="text-left">
                <div class="grey--text mb-2 caption">Name</div>
                <div class="font-weight-medium">
                  {{ appDetails.display_name + ' - ' + appDetails.service }}
                </div>
              </v-col>
              <v-col cols="4" class="text-left">
                <div class="grey--text mb-2 caption">Version</div>
                <div class="font-weight-medium">
                  {{ isFetchingAppStatus ? '-' : selectedVersion }}
                </div>
              </v-col>
            </v-row>
          </v-stepper-content>

          <v-stepper-content step="4">
            <v-row>
              <v-col cols="12" class="text-left">
                <div class="font-weight-large">Successfully installed {{ appDetails.display_name }} app.</div>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4" class="text-left">
                <div class="grey--text caption mb-2">Name</div>
                <div class="font-weight-medium">
                  {{ appDetails.display_name }}
                </div>
              </v-col>
              <v-col cols="4" class="text-left">
                <div class="grey--text caption mb-2">
                  <span>Version</span>
                </div>
                <div class="font-weight-medium">
                  {{ selectedVersion }}
                </div>
              </v-col>
            </v-row>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-card-text>
    <v-card-actions>
      <v-btn
        color="primary"
        @click="next"
        :loading="multiStepViewConfig[currentView].loader"
        v-if="multiStepViewConfig[currentView].hasForward"
      >
        {{
          typeof multiStepViewConfig[currentView].buttonLabel === 'function'
            ? multiStepViewConfig[currentView].buttonLabel(serviceStatus)
            : multiStepViewConfig[currentView].buttonLabel
        }}
      </v-btn>
      <v-btn @click="cancel" text> Cancel </v-btn>
    </v-card-actions>
  </v-card>

  <!-- <v-progress-circular indeterminate size="64"></v-progress-circular> -->
</template>
<script>
import AppStatusChip from '../Services/AppStatusChip.vue';
import TextFieldDisplay from '../Common/TextFieldDisplay.vue';
import * as appsService from '../../services/appsService';
import { RUBIX_APP_STATUS_CONTROL_ACTIONS, ALWAYS_RUNNING_RUBIX_APP } from '../../constants/appConstant';

function shouldStopRubixApp(serviceInfo) {
  return serviceInfo.status && ALWAYS_RUNNING_RUBIX_APP.RUBIX_PLAT !== serviceInfo.service;
}

export default {
  name: 'apps-download-multi-step',
  props: ['app-details', 'close-dialog'],
  components: { AppStatusChip, TextFieldDisplay },
  data() {
    return {
      valid: false,
      currentView: 1,
      appReleases: [],
      serviceStatus: {},
      isFetchingAppStatus: false,
      selectedVersion: null,
      multiStepViewConfig: {
        1: {
          back: 1,
          forward: 2,
          title: 'App Status',
          hasBack: false,
          hasForward: true,
          buttonLabel: serviceInfo => {
            if (shouldStopRubixApp(serviceInfo)) {
              return 'Stop';
            }
            return 'Continue';
          },
          loader: false,
        },
        2: {
          back: 1,
          forward: 3,
          title: 'Download App',
          hasBack: true,
          hasForward: true,
          buttonLabel: 'Download',
          loader: false,
        },
        3: {
          back: 2,
          forward: 4,
          title: 'Install App',
          hasBack: true,
          hasForward: true,
          buttonLabel: 'Install',
          loader: false,
        },
        4: {
          back: 3,
          forward: 4,
          title: 'Installation Complete',
          hasBack: true,
          hasForward: true,
          buttonLabel: 'Ok',
          loader: false,
        },
      },
      loading: true,
      rules: {
        required: value => !!value || 'Required.',
      },
      versionRules: [value => !!value || 'Version No. is required.'],
    };
  },
  async mounted() {
    const appName = this.appDetails.service;
    this.loading = true;
    await this.fetchAppStatus();
    await appsService
      .fetchAppReleases(appName)
      .then(releases => {
        this.appReleases = releases;
        this.loading = false;
      })
      .catch(err => {
        console.error(err);
      })
      .finally(() => {
        this.loading = false;
      });
  },
  methods: {
    back() {
      this.currentView = this.currentView - 1;
    },
    cancel() {
      this.closeDialog(true);
    },
    async next(step) {
      const currentView = this.currentView;
      switch (currentView) {
        case 1:
          this.stopAndContinue();
          break;
        case 2:
          this.download();
          break;
        case 3:
          this.install();
          break;
        case 4:
          this.continue();
          break;
        default:
          this.currentView++;
          break;
      }
    },
    stopAndContinue() {
      const serviceStatus = this.serviceStatus.status;
      const serviceName = this.serviceStatus.service;
      if (shouldStopRubixApp(this.serviceStatus)) {
        this.spinLoader(true);
        appsService
          .changeServiceStatus(RUBIX_APP_STATUS_CONTROL_ACTIONS.STOP, serviceName)
          .then(data => {
            this.spinLoader(false);
            this.currentView++;
            return this.fetchAppStatus();
          })
          .catch(err => {
            this.spinLoader(false);
          });
      } else {
        this.currentView++;
      }
    },
    continue() {
      this.closeDialog(true);
    },
    spinLoader(value) {
      this.multiStepViewConfig[this.currentView].loader = value;
    },
    download() {
      const valid = this.$refs.downloadForm.validate();
      if (!valid) {
        return;
      }
      this.spinLoader(true);

      const data = {
        service: this.appDetails.service,
        version: this.selectedVersion,
      };
      appsService
        .downloadApp(data)
        .then(res => {
          this.spinLoader(false);
          this.currentView++;
        })
        .catch(err => {
          if (err && err.message) {
            this.versionRules.push(() => err.message);
            this.$refs.downloadForm.validate();
            this.versionRules.pop();
          }
          this.spinLoader(false);
        })
        .finally(() => {});
    },
    install() {
      this.spinLoader(true);
      const data = {
        service: this.appDetails.service,
        version: this.selectedVersion,
      };

      appsService
        .installApp(data)
        .then(res => {
          this.spinLoader(false);
          this.fetchAppStatus();
          this.currentView++;
        })
        .catch(err => {
          this.spinLoader(false);
          this.currentView--;
          if (err && err.message) {
            this.versionRules.push(() => err.message);
            this.$refs.downloadForm.validate();
            this.versionRules.pop();
          }
        });
    },
    fetchAppStatus() {
      this.isFetchingAppStatus = true;
      return appsService
        .fetchAppStatus(this.appDetails.service, { restrictToast: true })
        .then(data => {
          this.selectedVersion = data.version;
          this.serviceStatus = data;
        })
        .catch(err => {
          this.selectedVersion = null;
          this.serviceStatus = {};
        })
        .finally(() => {
          this.isFetchingAppStatus = false;
        });
    },
  },
};
</script>
