<template>
  <v-card :loading="isFetchingConfig">
    <v-card-title>{{ appDetail.display_name }} | Edit Config</v-card-title>
    <v-card-text>
      <v-container>
        <v-jsoneditor
          v-if="isConfigJSON"
          :options="{ mode: 'code' }"
          v-model="config"
          :plus="false"
          height="400px"
        ></v-jsoneditor>
        <v-textarea
          v-if="!isConfigJSON"
          outlined
          name="input-7-4"
          label="Enter ENV config"
          v-model="config"
        ></v-textarea>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-btn color="primary" @click="saveConfig" :loading="isSavingConfig"> Save </v-btn>
      <v-btn @click="closeDialog" text> Cancel </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import * as appsService from '../../services/appsService';
import { APP_CONFIG_TYPES, JSON_CONFIG_TYPE } from '../../constants/appConstant';
export default {
  name: 'app-edit-config',
  props: ['appDetail', 'closeDialog'],
  data() {
    return {
      config: null,
      isFetchingConfig: false,
      isSavingConfig: false,
      isConfigJSON: false,
    };
  },
  mounted() {
    this.fetchAppConfig();
    this.isConfigJSON = this.isJSON(this.appDetail.app_type);
  },
  methods: {
    isJSON(appType) {
      return APP_CONFIG_TYPES[appType] === JSON_CONFIG_TYPE;
    },
    fetchAppConfig() {
      const serviceName = this.appDetail.service;
      const appType = this.appDetail.app_type;
      this.isFetchingConfig = true;
      appsService
        .fetchAppConfig(serviceName, appType, { restrictToast: true })
        .then(data => {
          this.config = data;
        })
        .catch(err => {})
        .finally(() => {
          this.isFetchingConfig = false;
        });
    },
    saveConfig() {
      const serviceName = this.appDetail.service;
      const appType = this.appDetail.app_type;
      this.isSavingConfig = true;
      appsService
        .saveAppConfig(serviceName, appType, this.config)
        .then(data => {
          this.$toasted.show(`Successfully updated config for ${this.appDetail.display_name}`);
          this.closeDialog();
        })
        .catch(err => {
          this.$toasted.show(`Failed to update config for ${this.appDetail.display_name}`, { type: 'error' });
        })
        .finally(() => {
          this.isSavingConfig = false;
        });
    },
  },
};
</script>
